<template>
  <div class="scroller">
    <div class="header">
      <h2>{{title}}</h2>
      <a href="#">更多</a>
    </div>
    <div class="content">
      <slot name="promItem"></slot>
      <ul class="hasCover" v-if="type === 'hasCover'">
        <li v-for="item in items">
          <a href="#">
            <img :src="item.images.large" alt="">
            <span class="title">{{item.title}}</span>
            <div class="rank">
              <span class="star-full"></span>
              <span class="star-full"></span>
              <span class="star-full"></span>
              <span class="star-gray"></span>
              <span class="star-gray"></span>
              <span>6.9</span>
            </div>
          </a>
        </li>
      </ul>
      <ul class="onlyString" v-if="type === 'onlyString'">
        <li v-for="item in items" style="border-color: #FFAC2D;">
          <a :href="item.href" v-if="!item.line" :style="{color: item.color}">{{item.title}}</a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'scroller',
    props: ['title', 'type', 'items'],
    data () {
      return {

      }
    }
  }
</script>

<style lang="scss" scoped>
.scroller {
  padding-top: 10px;
}
.header {
  height: 26px;
  line-height: 26px;
  padding: 0 16px;

  a {
    float: right;
    &:last-child {
      color: #42bd56;
    }
  }

  h2 {
    display: inline-block;
    margin: 0;
    font-size: 16px;
    font-weight: normal;
    color: #111;
  }
}
.content {
  box-sizing: content-box;
}
ul {
  margin: 0;
  padding: 8px 0;
}
li {
  list-style: none;
  padding: 0;
}

.hasCover {
  overflow-x: auto;
  white-space: nowrap;
  text-align: center;
  .title {
    display: block;
    max-width: 100%;
    margin-top: 10px;
    line-height: 16px;
    color: #111;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-wrap: normal;
  }
  li {
    display: inline-block;
    width: 100px;
    margin-left: 8px;
  }
  li:first-child {
    padding-left: 8px;
  }
  img {
    height: 150px;
  }
  .rank {
    margin-top: 4px;
  }
  .star-full {
    display: inline-block;
    width: 10px;
    height: 10px;
    background-image: url(data:image/png;base64,);
    background-size: 10px 10px;
  }
  .star-gray {
    display: inline-block;
    width: 10px;
    height: 10px;
    background-image: url(data:image/png;base64,);
    background-size: 10px 10px;
  }
  .rank span:last-child {
    color: #aaa;
  }
}
.onlyString {
  overflow-x: auto;
  white-space: nowrap;
  li {
    display: inline-block;
    margin: 0 0 8px 16px;
    font-size: 16px;
    border: solid 1px;
    border-radius: 4px;
    vertical-align: middle;
  }
  a {
    height: 50px;
    line-height: 50px;
    padding: 0 24px;
    letter-spacing: 1.6px;
    overflow: auto;
    display: block;
    text-align: center;
  }
  li:empty {
    width: 100%;
    display: block;
    height: 1px;
    border: 0;
    margin: 0;
  }
}

</style>



